






















































































































































































import { Vue, Component, Ref } from 'vue-property-decorator';
import {
  getTable, // 列表
  importScenes, //场景导入
  exportScenes, // 场景导出
  getDept, //所属部门
  delScenes, // 删除场景
  check, // 删除启用
  getTags, // 删除启用
  changeScenesType // 场景转换
} from '@api/businessScenarioManage';
// 字段设置
import codeSet from './component/codeSet.vue';
// 新增修改
import dataSet from './component/dataSet.vue';
// 标签设置
import tagViewSet from './component/tagViewSet.vue';
import cloneDeep from 'lodash/cloneDeep';

@Component({
  components: { dataSet, codeSet, tagViewSet }
})
export default class TaskLibraryManage extends Vue {
  name: 'TaskLibraryManage';
  @Ref() dataSetLog: any; // 新增 修改 复制
  @Ref() codeSetLog: any; // 字段设置
  @Ref() netUpload: any; // 导入

  private tableData: Array<any> = [];
  private total = 0;
  private page = 1;
  private limit = 10;
  private loading = false;
  private selections: Array<any> = [];

  // 导入
  private importDialogVisible = false;
  private importFiles: Array<any> = [];
  private importLoading = false;

  // 搜索条件
  private queryParams: any = {
    scenesState: 'all',
    scenesDept: [],
    scenesName: '',
    scenesType: 'all',
    tagName: []
  };
  // 搜索标签
  private processTagsList: Array<any> = [];
  private processTagsListData: Array<any> = [];

  private codes: Array<any> = []; // 字段设置
  //所属部门
  private deptOptions = [];
  private typeOptions = [
    {
      value: 'all',
      label: '全部'
    },
    {
      value: 1,
      label: '业务场景'
    },
    {
      value: 2,
      label: '项目场景'
    }
  ];
  private stateOptions = [
    {
      value: 'all',
      label: '全部'
    },
    {
      value: 0,
      label: '未启用'
    },
    {
      value: 1,
      label: '已启用'
    }
  ];
  columns() {
    const typeTrans = {
      all: '全部',
      '1': '业务场景',
      '2': '项目场景'
    };
    return [
      {
        prop: '',
        type: 'selection',
        label: '序号',
        width: '60'
      },
      {
        prop: 'scenesName',
        label: '场景名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'scenesDeptName',
        label: '所属部门',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '60'
        // render: (h, record) => {
        //   return h('span', typeTrans[record.record.scenesDept]);
        // }
      },
      {
        prop: 'scenesType',
        label: '场景类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '60',
        render: (h, record) => {
          return h('span', typeTrans[record.record.scenesType]);
        }
      },
      {
        prop: 'checked',
        label: '场景状态',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '60',
        render: (h, record) => {
          return h('span', record.record.checked === 0 ? '未启用' : '已启用');
        }
      },
      {
        prop: 'editor',
        label: '修改人',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '60'
      },
      {
        prop: 'editorTime',
        label: '修改时间',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '60'
      },
      {
        align: 'center',
        label: '操作',
        width: '240',
        render: (h: any, { record }: any) => {
          const actions = [
            { name: '任务管理', action: 'taskManage' },
            { name: '字段设置', action: 'codeSet' },
            { name: '转换', action: 'transData', disabled: false },
            { name: '删除', action: 'delRow' },
            { name: '导出', action: 'exportScenes' }
          ];
          actions[2].disabled = record.scenesType === '1' ? true : false;
          return h('div', {}, [
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                on: {
                  click: () => this.edit(record)
                }
              },
              '编辑'
            ),
            h(
              'span',
              {
                style: {
                  padding: '0 6px'
                }
              },
              '|'
            ),
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                on: {
                  click: () => this.copy(record)
                }
              },
              '复制'
            ),
            h(
              'span',
              {
                style: {
                  padding: '0 6px'
                }
              },
              '|'
            ),
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                on: {
                  click: () => this.checkIt(record)
                }
              },
              record.checked ? '停用' : '启用'
            ),
            h(
              'span',
              {
                style: {
                  padding: '0 6px'
                }
              },
              '|'
            ),
            h(
              'el-dropdown',
              {
                on: {
                  command: this.handleCommand.bind(this, record)
                },
                props: {
                  trigger: 'click'
                }
              },
              [
                h(
                  'el-button',
                  {
                    props: {
                      type: 'text'
                    }
                  },
                  [
                    '更多',
                    h('svg-icon', {
                      props: {
                        iconClass: 'icon_down_backups'
                      },
                      class: 'icon'
                    })
                  ]
                ),
                h(
                  'el-dropdown-menu',
                  {
                    slot: 'dropdown'
                  },
                  actions.map((item) => {
                    return h(
                      'el-dropdown-item',
                      {
                        props: {
                          command: item.action,
                          disabled: item.disabled
                        }
                      },
                      item.name
                    );
                  })
                )
              ]
            )
          ]);
        }
      }
    ];
  }

  private addVisible = false; // // 新增 修改 复制
  private data: Record<string, any>; // 新增 修改 复制数据
  private isCopy = false; // 新增 修改 判断是不是复制

  private tagVisible = false; // // 新增 修改 复制

  private codeSetVisible = false; // 字段设置

  // 查看标签库
  viewTags() {
    this.tagVisible = true;
  }
  cancelTagView() {
    this.tagVisible = false;
    this.getTags();
  }
  // 列表更多
  handleCommand(row: any, command: any) {
    this[command](row);
  }
  // 新 增
  add() {
    this.data = {
      scenesType: '',
      scenesName: '',
      scenesDesc: '',
      scenesDept: '',
      tagName: [],
      scenesLaunching: {
        scenesTimingList: [
          {
            startDate: '',
            execTime: '',
            endDate: '',
            startFreq: '',
            startUnit: '',
            offsetDay: '',
            offsetUnit: ''
          }
        ]
      }
    };
    this.isCopy = false;
    this.addVisible = true;
  }

  //修改
  edit(row: any) {
    console.log('edit', row);
    this.data = cloneDeep(row);
    this.isCopy = false;
    this.addVisible = true;
  }
  //复制
  copy(row: any) {
    const data: any = cloneDeep(row);
    this.data = data;
    data.scenesName = '';
    this.isCopy = true; // 复制
    this.addVisible = true;
  }
  // 新增弹窗取消
  cancelData() {
    this.addVisible = false;
    this.getTable();
    this.getTags();
  }
  // 获取标签
  getTags() {
    this.processTagsList = [];
    getTags().then((res) => {
      if (res.error) {
        this.$message.error(`获取失败${res.error.message}`);
      } else {
        console.log(this.processTagsList);
        this.processTagsList = this.processTagsList.concat(res.data);
      }
    });
  }
  // 获取数据
  async getTable() {
    this.loading = true;
    const { limit, page, queryParams } = this;
    queryParams.tagName = [];
    this.processTagsListData.map((item) => {
      queryParams.tagName.push(item.tagName);
    });
    const data = {
      page: page,
      pageSize: limit,
      ...queryParams
    };
    try {
      const res: any = await getTable(data);
      this.tableData = res.data.rows;
      this.total = res.data.total;
    } catch (error) {
      this.$message.error(`获取数据失败`);
    }
    this.loading = false;
  }

  //  获取所属部门
  async getDept() {
    try {
      const res: any = await getDept();
      if (!res.error) {
        this.deptOptions = res.data;
      } else {
        this.$message.error('获取所属部门失败');
      }
    } catch (error) {
      this.$message.error('获取所属部门失败');
    }
  }

  // 搜索
  handleSearch() {
    this.page = 1;
    this.getTable();
  }

  // 重置
  handleReset() {
    this.queryParams = {
      scenesState: 'all',
      scenesDept: [],
      scenesName: '',
      scenesType: 'all',
      tagName: []
    };
    this.handleSearch();
  }

  // 搜索标签点击
  selectTag(tag: any) {
    if (tag.tagName === '全部') {
      if (this.processTagsListData.length === this.processTagsList.length) {
        this.processTagsListData = [];
      } else {
        this.processTagsListData = cloneDeep(this.processTagsList);
      }
    } else {
      const tagIndex = this.processTagsListData.findIndex(function(item) {
        return item.tagName === tag.tagName;
      });
      if (tagIndex === -1) {
        this.processTagsListData.push(tag);
        if (
          this.processTagsListData.length ===
          this.processTagsList.length - 1
        ) {
          this.processTagsListData.push({
            tagName: '全部',
            tagCount: this.total
          });
        }
      } else {
        this.processTagsListData.splice(tagIndex, 1);
        const allTag = this.processTagsListData.findIndex(function(item) {
          return item.tagName === '全部';
        });
        if (allTag !== -1) {
          this.processTagsListData.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }

  // 多选
  selectionChange(val: Array<any>) {
    this.selections = val;
  }
  //转换
  transData(row: any) {
    const { id } = row;
    this.$confirm(
      `是否将<span style='color:red'>${row.scenesName}</span>的场景类型转换为业务场景`,
      '操作提醒',
      {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    ).then(() => {
      console.log(row);
      changeScenesType({ id }).then((res) => {
        if (!res.error) {
          this.$message.success('转换成功');
          row.scenesType = '1';
        }
      });
    });
  }
  // 导出
  exportScenes(row: any) {
    exportScenes({ scenesId: row.id }).then((res) => {
      console.log(res);
    });
  }
  //任务管理
  taskManage(row: any) {
    const { id, scenesType } = row;
    this.$router.push({
      path: '/taskConfigCenter/taskLibraryManage',
      query: { scenesId: id, scenesType }
    });
  }

  // 字段设置弹窗展示
  codeSet(row: any) {
    this.codes = row;
    this.codeSetVisible = true;
  }
  // 字段设置弹窗取消
  cancelCodeSet() {
    this.codeSetVisible = false;
    this.getTable();
  }

  // 启用
  checkIt(row: any) {
    const arr: Array<any> = [];
    arr.push(row.id);
    const msg = row.checked === 0 ? '启用' : '停用';
    check(arr).then((res) => {
      if (!res.error) {
        const index = this.tableData.indexOf(row);
        this.$message.success(`${msg}成功`);
        this.tableData[index].checked = (this.tableData[index].checked + 1) % 2;
      } else {
        this.$message.error(`${msg}失败:${res.error.message}`);
      }
    });
  }
  // 批量操作
  handleCheckCommand(command) {
    this[command]();
  }
  // 全部停用
  checkAll() {
    const arr: Array<any> = [];
    const allNotCheckedFlag = this.selections.every((item) => {
      return item.checked === 0;
    });
    if (!allNotCheckedFlag) {
      this.$message.error('请确认数据都是未启用型数据');
    } else {
      this.selections.map((item) => {
        arr.push(item.id);
      });
      check(arr).then((res) => {
        console.log(res);
        if (!res.error) {
          this.getTable();
          this.$message.success('启用成功');
        } else {
          this.$message.error(`启用失败：${res.error.message}`);
        }
      });
    }
  }
  // 全部启用
  notCheckAll() {
    const arr: Array<any> = [];
    const allCheckedFlag = this.selections.every((item) => {
      return item.checked === 1;
    });
    console.log('allCheckedFlag', allCheckedFlag);
    if (!allCheckedFlag) {
      this.$message.error('请确认数据都是启用型数据');
    } else {
      this.selections.map((item) => {
        arr.push(item.id);
      });
      check(arr).then((res) => {
        console.log(res);
        if (!res.error) {
          this.getTable();
          this.$message.success('停用成功');
        } else {
          this.$message.error(`停用失败：${res.error.message}`);
        }
      });
    }
  }
  // 删除
  delRow(row: any) {
    let data: Array<any> = [];
    if (this.selections.length) {
      data = this.selections;
    } else {
      data.push(row.id);
    }
    delScenes(data).then((res) => {
      console.log(res);
      if (res.error) {
        this.$message.error(`删除失败：${res.error.message}`);
      } else {
        this.getTable();
      }
    });
  }
  // 翻页
  handleCurrent(val) {
    this.page = val;
    this.getTable();
  }
  // 翻页
  handleSize(val) {
    this.page = 1;
    this.limit = val;
    this.getTable();
  }

  // 文件导入
  importScenes() {
    this.importDialogVisible = true;
  }
  doUpload() {
    if (this.importFiles.length === 0) {
      this.$message.error('请先选择要导入或上传的文件！');
    } else {
      this.importLoading = true;
      const uploadForm = new FormData();
      this.importFiles.forEach((item) => {
        uploadForm.append('file', item.raw);
      });
      importScenes(uploadForm).then((res) => {
        if (!res.error) {
          this.$message.success('导入成功');
          this.closeDialog();
        } else {
          this.$message.error(`导入失败:${res.error}`);
        }
      });
    }
  }
  closeDialog() {
    this.netUpload.clearFiles();
    this.importLoading = false;
    this.importDialogVisible = false;
  }
  // 导入文件
  handleUploadFileChange(file, fileList) {
    this.importFiles = fileList;
  }
  // 移除待上传文件
  onRemove(file, fileList) {
    this.importFiles = fileList;
  }

  created() {
    this.getTable(); // 获取列表
    this.getDept(); // 获取所属部门
    this.getTags(); // 获取标签
  }
}
