

























































































































































































import { Component, Vue, Prop, Emit, Watch, Ref } from 'vue-property-decorator';
import {
  getTags,
  modifyScenes,
  copyScenes,
  queryScenesDetail
} from '@api/businessScenarioManage';
import numberInput from '@components/NumberInput/index.vue';
import cloneDeep from 'lodash/cloneDeep';
interface FormData {
  scenesType: string;
  scenesName: string;
  scenesDesc: string;
  scenesDept: string; // 所属部门
  tagId: Array<any>;
}

@Component({
  components: { numberInput }
})
export default class DataSet extends Vue {
  name: 'DataSet';
  @Ref() createForm: any;
  @Emit('cancelEmit')
  cancelCreat() {}
  @Ref() datasetForm: any;
  @Prop() visible: boolean;
  @Prop() isCopy: boolean;
  @Prop() data: Record<string, any>;
  @Prop() deptOptions: Record<string, any>;
  private formData: any = {
    scenesType: '',
    scenesName: '',
    scenesDesc: '',
    scenesDept: '', // 所属部门
    tagId: [],
    scenesLaunching: {
      scenesTimingList: [
        {
          startDate: '',
          execTime: '',
          endDate: '',
          startFreq: '',
          startUnit: '',
          offsetDay: '',
          offsetUnit: ''
        }
      ]
    }
  };

  private isTime = false;
  private loading = false;
  private inputVisible = false;
  private inputValue = '';
  private tagOptions: Array<any> = [];
  private selectedTags: Array<any> = [];

  private frequencyTypeList: Array<any> = [
    { value: '8', label: '一次性' },
    { value: '7', label: '年' },
    { value: '6', label: '季' },
    { value: '5', label: '月' },
    { value: '4', label: '周' },
    { value: '3', label: '天' },
    { value: '2', label: '时' },
    { value: '1', label: '分' },
    { value: '0', label: '秒' }
  ];

  private deviationTypeList: Array<any> = [
    { value: '0', label: '工作日' },
    { value: '1', label: '自然日' }
  ];

  @Watch('visible')
  visibleChange(val) {
    if (val) {
      this.createForm && this.createForm.resetFields();
      this.getTags();
      // this.queryScenesDetail();
    }
  }

  @Watch('data')
  dataChange(val) {
    console.log(val);
    this.selectedTags = [];
    if (
      JSON.stringify(val.scenesLaunching) !==
        '{"scenesTimingList":[{"startDate":"","execTime":"","endDate":"","startFreq":"","startUnit":"","offsetDay":"","offsetUnit":"}]}' &&
      val.scenesLaunching.scenesTimingList.length > 0 &&
      JSON.stringify(val.scenesLaunching) !== '{"scenesTimingList":[]}'
    ) {
      this.isTime = true;
    }
    this.$nextTick(() => {
      // this.formData = cloneDeep(val);
      Object.assign(this.formData, val);
      if (!this.isTime) {
        this.formData.scenesLaunching = {
          scenesTimingList: [
            {
              startDate: '',
              execTime: '',
              endDate: '',
              startFreq: '',
              startUnit: '',
              offsetDay: '',
              offsetUnit: ''
            }
          ]
        };
      }

      console.log(cloneDeep(this.formData));
    });
  }

  get title() {
    return this.data && this.data.id ? '修改' : '新增';
  }

  // queryScenesDetail() {
  //   queryScenesDetail({ id: this.data.id }).then((res) => {
  //     console.log(res);
  //   });
  // }
  private rules: any = {
    scenesType: [
      { required: true, message: '请输入场景名称', trigger: 'change' }
    ],
    scenesName: [
      { required: true, message: '请输入场景名称', trigger: 'blur' }
    ],
    scenesDept: [
      { required: true, message: '请选择所属部门', trigger: 'change' }
    ]
    // 此处莫名bug
    // startDate: {
    //   required: true,
    //   validator: (rule: any, value: any, callback: any) => {
    //     console.log('1121', value);
    //     console.log(
    //       '1121',
    //       this.formData.scenesLaunching.scenesTimingList[0].startDate
    //     );
    //     if (
    //       this.formData.scenesLaunching.scenesTimingList[0].startDate === ''
    //     ) {
    //       return callback(new Error('请选择开始时间'));
    //     } else {
    //       return callback();
    //     }
    //   },
    //   trigger: 'blur'
    // },
    // endDate: {
    //   required: true,
    //   validator: (rule: any, value: any, callback: any) => {
    //     console.log(123123132);
    //     if (this.formData.scenesLaunching.scenesTimingList[0].endDate === '') {
    //       return callback(new Error('请选择结束时间'));
    //     }
    //     if (
    //       this.formData.scenesLaunching.scenesTimingList[0].startDate >
    //       this.formData.scenesLaunching.scenesTimingList[0].endDate
    //     ) {
    //       return callback(new Error('结束时间须大于开始时间'));
    //     } else {
    //       console.log(123);
    //       return callback();
    //     }
    //   },
    //   trigger: 'change'
    // }
  };

  private sceneOptions: Array<any> = [
    {
      value: '1',
      label: '业务场景'
    },
    {
      value: '2',
      label: '项目场景'
    }
  ];

  //  获取标签
  async getTags() {
    this.loading = true;
    const res: any = await getTags();
    if (!res.error) {
      this.tagOptions = res.data;
      this.formData.tagId && this.formData.tagId.length && this.selectTag();
    } else {
      this.$message.error(`获取标签列表失败:${res.error.message}`);
    }
    this.loading = false;
  }

  // 选中后 展示tag
  selectTag() {
    this.selectedTags = [];
    const bakArr = cloneDeep(this.formData.tagId);
    this.formData.tagId.map((item) => {
      this.tagOptions.map((val) => {
        if (item === val.id) {
          console.log(val);
          this.selectedTags.push(val);
          bakArr.splice(bakArr.indexOf(item), 1);
        }
      });
    });
    console.log(cloneDeep(bakArr));
    bakArr.length &&
      bakArr.forEach((item) => {
        this.selectedTags.push({ id: item, tagName: '' });
      });
  }
  // 关闭标签
  closeSelectedTags(tag) {
    console.log(tag);
    if (!this.isCopy) {
      this.selectedTags.splice(this.selectedTags.indexOf(tag), 1);
      this.formData.tagId.forEach((item, index) => {
        if (item === tag.id) {
          this.formData.tagId.splice(index, 1);
        }
      });
    }
  }

  // 修改
  editData() {
    this.createForm.validate((valid) => {
      if (valid) {
        const { id, scenesName } = this.formData;
        // 不是定时发起的话
        if (!this.isTime) {
          this.formData.scenesLaunching = {
            scenesTimingList: [
              {
                startDate: '',
                execTime: '',
                endDate: '',
                startFreq: '',
                startUnit: '',
                offsetDay: '',
                offsetUnit: ''
              }
            ]
          };
        }
        if (this.isCopy) {
          const data = {
            id,
            targetScenesName: scenesName
          };
          copyScenes(data).then((res) => {
            if (!res.error) {
              this.cancelDialog();
            } else {
              this.$message.error(`保存失败:${res.error.message}`);
            }
          });
        } else {
          modifyScenes(this.formData).then((res) => {
            if (!res.error) {
              this.cancelDialog();
            } else {
              this.$message.error(`保存失败:${res.error.message}`);
            }
          });
        }
      } else {
        return false;
      }
    });
  }

  // 关联任务
  associat() {
    const { scenesDept } = this.formData;
    this.deptOptions.forEach((item) => {
      if (item.id === scenesDept) {
        this.formData.scenesDeptName = item.deptName;
        return;
      }
    });
    this.createForm.validate((valid) => {
      console.log(valid);
      if (valid) {
        this.$router.push({
          path: '/taskConfigCenter/addTasks',
          query: { ...this.formData }
        });
        this.cancelDialog();
      } else {
        return false;
      }
    });
  }

  // 取消
  cancelDialog() {
    // this.createForm && this.createForm.resetFields();
    this.formData = {
      scenesType: '',
      scenesName: '',
      scenesDesc: '',
      scenesDept: '', // 所属部门
      tagId: [],
      scenesLaunching: {
        scenesTimingList: [
          {
            startDate: '',
            execTime: '',
            endDate: '',
            startFreq: '',
            startUnit: '',
            offsetDay: '',
            offsetUnit: ''
          }
        ]
      }
    };
    this.createForm.resetFields();
    this.cancelCreat();
  }

  // 删除未上传库内tag
  handleClose(tag: any) {
    this.formData.tagId.splice(this.formData.tagId.indexOf(tag), 1);
  }
}
