
























import { Component, Vue, Prop, Emit, Watch, Ref } from 'vue-property-decorator';
import {
  setScenesColumn,
  queryScenesColumn
} from '@api/businessScenarioManage';
@Component
export default class CodeSet extends Vue {
  name: 'CodeSet';
  @Prop() visible;
  @Prop() data;
  @Ref() baseTable;
  @Watch('data', { deep: true, immediate: true })
  dataChange(val) {
    this.scenesId = val.id;
    if (val.id) {
      this.queryScenesColumn();
    }
  }
  @Emit('cancelCodeSet')
  cancelCode() {}

  private tableData: Array<any> = [];
  private loading = false;
  private scenesId;
  columns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '70'
      },
      {
        prop: 'columnCode',
        label: 'code',
        'header-align': 'left',
        'min-width': '100',
        editer: {
          type: 'input',
          key: 'columnCode',
          placeholder: '请输入参数code'
        }
      },
      {
        prop: 'columnName',
        label: '名称',
        'header-align': 'left',
        'min-width': '100',
        editer: {
          type: 'input',
          key: 'columnName',
          placeholder: '请输入参数名称'
        }
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        render(h: any, { record }: any) {
          if (record.addButton === true) {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text',
                    disabled: record.delButton
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              ),
              h(
                'span',
                {
                  style: 'padding: 0 5px'
                },
                '|'
              ),
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.addEdit(record)
                  }
                },
                '添加'
              )
            ]);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text',
                    disabled: record.delButton
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              )
            ]);
          }
        }
      }
    ];
  }
  // 确定
  confirm() {
    this.loading = true;
    const scenesColumns: Array<any> = [];
    const { scenesId, tableData } = this;
    tableData.forEach((item) => {
      if (item.columnCode !== '' && item.columnName !== '') {
        scenesColumns.push({
          columnCode: item.columnCode,
          columnName: item.columnName
        });
      }
    });
    // this.confirmCode(this.tableData);
    const data = {
      scenesColumns,
      scenesId
    };
    setScenesColumn(data).then((res) => {
      if (!res.error) {
        this.loading = false;
        this.cancelDialog();
      }
    });
  }

  //取消
  cancelDialog() {
    this.cancelCode();
  }

  // 删除
  deleteEdit(row) {
    if (this.tableData.length > 1) {
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].id === row.id) {
          this.tableData.splice(i, 1);
        }
      }
      const addView = this.tableData.slice(-1);
      addView.forEach((item) => {
        this.$set(item, 'addButton', true);
      });
    }
  }

  // 添加
  addEdit(row) {
    const addData = Object.assign({}, row);
    for (const i in addData) {
      if (i === 'id') {
        addData[i] = this.tableData.length;
      } else if (i !== 'edits') {
        addData[i] = '';
      }
    }
    addData.isCompatible = '1';
    addData.delButton = false;
    addData.isEdit = true;
    this.tableData.unshift(addData);
  }

  queryScenesColumn() {
    this.loading = true;
    queryScenesColumn({ id: this.scenesId }).then((res) => {
      if (!res.error) {
        this.tableData = res.data && res.data.scenesColumns;
        this.tableData.map((item, index) => {
          item.id = index;
          item.addButton = index === this.tableData.length - 1 ? true : '';
          item.isCompatible = '1';
          item.isEdit = true;
          item.edits = [0, 1, 1, 0];
        });
        if (!this.tableData.length) {
          this.tableData.push({
            id: 1,
            addButton: true,
            isCompatible: '1',
            isEdit: true,
            edits: [0, 1, 1, 0],
            columnCode: '',
            columnName: ''
          });
        }
      } else {
        this.$message.error(res.error.message);
      }
      this.loading = false;
    });
  }
}
