<template>
  <el-input
    type="number"
    v-model="val"
    @input="changeData"
    :disabled="disabled"
    @keydown.native="numberInputLimit"
  ></el-input>
</template>

<script>
export default {
  name: 'NumberInput',
  model: {
    prop: 'val',
    event: 'input'
  },
  props: {
    val: {
      type: [String, Number],
      required: true
    },
    disabled: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    // 数字输入框校验
    numberInputLimit(e) {
      const key = e.key;
      // 不允许输入'e'和'.'和+ -
      if (key === 'e' || key === '.' || key === '+' || key === '-') {
        e.returnValue = false;
        return false;
      }
      return true;
    },
    changeData(val) {
      this.$emit('input', val);
    }
  }
};
</script>

<style></style>
