















































import { getTags, addTags, deleteTag } from '@api/businessScenarioManage';
import { Component, Vue, Prop, Emit, Watch, Ref } from 'vue-property-decorator';

@Component
export default class DataSet extends Vue {
  name: 'DataSet';
  @Ref() createForm: any;
  @Emit('cancelEmit')
  cancelVisible() {}
  @Prop() visible: boolean;

  @Watch('visible')
  changeVisible(val) {
    if (val) {
      this.addedTagList = [];
      this.loadTags();
    }
  }
  private loading = false;
  private value = '';
  private tagList: Array<any> = [];
  private addedTagList: Array<any> = [];
  // 获取标签
  loadTags(val = '') {
    this.loading = true;
    getTags(val).then((res) => {
      this.loading = false;
      if (res.error) {
        this.$message.error(`获取失败:${res.error.message}`);
      } else {
        this.tagList = res.data;
      }
    });
  }
  // 删除数据库内标签
  handleCloseTag(item, index) {
    console.log(item);
    deleteTag(item.id).then((res) => {
      console.log(res);
      if (!res.error) {
        this.tagList.splice(index, 1);
        this.$message.success(`删除成功:${res.error.message}`);
      }
    });
  }
  handleCloseAddedTag(index) {
    this.addedTagList.splice(index, 1);
  }

  // 创建标签
  createTag() {
    if (this.value.trim()) {
      !this.tagList.some((item) => item.tagName === this.value) &&
        this.addedTagList.indexOf(this.value) === -1 &&
        this.addedTagList.push(this.value);
      this.value = '';
    }
  }

  // 搜索标签
  searchTag() {
    console.log(this.value);
    this.loadTags(this.value);
  }

  // 确定
  confirmCreate() {
    addTags(this.addedTagList).then((res) => {
      if (!res.error) {
        this.cancelDialog();
      } else {
        this.$message.error(`创建失败:${res.error.message}`);
      }
    });
  }
  cancelDialog() {
    this.cancelVisible();
  }
}
